	import {mapState,mapMutations} from 'vuex';
	import uniLoadMore from '@/components/uni-load-more.vue';
	import mediaList from '@/components/media-list.vue';
	
	import myImage from '@/components/my-image.vue';
	var _self,timer = null,pageIndex=1;
	export default {
		components:{
			myImage,
			uniLoadMore,
			mediaList
		},
		data() {
			return {
				tabBars:[],
				tabIndex:0,
				catagoryIdList:[], //分类id
				// pageIndex:1,
				goodsList:[],
				isClickChange:false,
				scrollLeft:0,
				classId:'',
				loadingType: 0,
				totalPage:1,
				pageSize:10,
				hasNextPage:false,
				loadingText: '加载中...',
				contentText: {
					contentdown: "上拉显示更多",
					contentrefresh: "正在加载...",
					contentnomore: "没有更多数据了"
				}
			}
		},
		computed: mapState(['hasLogin', 'userName','token']),
		onLoad() {
			_self = this;
			if(!this.hasLogin){
				uni.reLaunch({
					url:'/pages/login/login/login'
				})
			}else {
				console.log('已经登录过了');
				this.getBabBars();
			}
		},
		methods: {
			toMine(){
				uni.switchTab({
					url:"/pages/tabBar/mine/mine"
				})
			},
			getBabBars(){
				uni.request({
					url: this.websiteUrl + 'shop/goods_class_list',
					method: 'POST',
					data:{
						pid: 122
					},
					dataType:'json',//服务器返回json格式数据
					header:{'Content-Type':'application/json','Token':this.token},
					success: res => {
						var res = res.data;
						if(res.code == 200){
							// console.log("获取tabBar列表成功："+JSON.stringify(res))
							this.tabBars = res.data;
							this.getCatagoryId(res.data);
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
			getCatagoryId(list){ //获取到每个tabar下的id
				let ary = [];
				for(var i = 0;i < list.length;i++){
					// this.catagoryIdList.push(list[i].id);
					ary.push(list[i].id)
				}
				this.catagoryIdList = ary;
				// console.log(this.catagoryIdList)
				this.classId = ary[this.tabIndex];
				this.getGoodsList();
			},
			getGoodsList(){
				pageIndex = 1;
				this.loadingType = 0;
				uni.showNavigationBarLoading();
				uni.request({
					url: this.websiteUrl + 'shop/goods_list',
					method: 'POST',
					data:{
						classId:this.classId,
						pageIndex:pageIndex,
						pageSize:this.pageSize
					},
					dataType:'json',//服务器返回json格式数据
					header:{'Content-Type':'application/json','Token':this.token},
					success: res => {
						var res = res.data;
						if(res.code == 200){
							var list = res.data.list;
							var data = res.data;
							this.goodsList = list;
							this.hasNextPage = data.hasNextPage;
							if(!data.hasNextPage){
								_self.loadingType = 2;
							}
							uni.hideNavigationBarLoading();
						}
						
					},
					fail: () => {},
					complete: () => {}
				});
			},
			getMoreGoodsList(){
				/* if(_self.loadingType != 0){
					return false;
				} */
				// _self.loadingType = 1;
				uni.showNavigationBarLoading();
				uni.request({
					url: this.websiteUrl + 'shop/goods_list',
					method: 'POST',
					data:{
						classId:this.classId,
						pageIndex:pageIndex,
						pageSize:this.pageSize
					},
					dataType:'json',//服务器返回json格式数据
					header:{'Content-Type':'application/json','Token':this.token},
					success: res => {
						var res = res.data;
						if(res.code == 200){
							var list = res.data.list;
							var data = res.data;
							if(data == null){
								_self.loadingType = 2;
								uni.hideNavigationBarLoading();
								return;
							}
							_self.goodsList = _self.goodsList.concat(list);
							_self.loadingType = 0;
							this.hasNextPage = data.hasNextPage;
							uni.hideNavigationBarLoading();
						}
						
					},
					fail: () => {},
					complete: () => {}
				});
			},
			getElSize(id){ //得到元素的size
				return new Promise((res,rej)=>{
					uni.createSelectorQuery().select("#" + id).fields({
						size:true,
						scrollOffset:true,
					},(data)=>{
						res(data);
					}).exec();
				})
			},
			async loadMore(){
				console.log('加载',pageIndex,_self.hasNextPage)
				pageIndex++;
				_self.loadingType = 1;
				if(_self.hasNextPage){
					_self.getMoreGoodsList();
				}else {
					_self.loadingType = 2;
				}
			},
			async tabTab(e){  //点击tabbar
				pageIndex = 1;
				// console.log(e.target.dataset.catogoryId)
				if(this.tabIndex === e.target.dataset.current){
					return false;
				}else {
					let tabBar = await this.getElSize("tab-bar"),
						tabBarScrollLeft = tabBar.scrollLeft;  //点击的时候记录并设置scrollLeft
						// console.log(tabBarScrollLeft);
					this.scrollLeft = tabBarScrollLeft;
					this.isClickChange = true;
					this.tabIndex = e.target.dataset.current;
					var catagoryId = e.target.dataset.catogoryId;
					this.classId = catagoryId;
					// this.getGoodsList();
					console.log('点击')
				}
			},
			async changeTab(e){
				let index = e.target.current;
				this.pageIndex = 1;
				this.setScrollLeft(index);
				this.classId = this.catagoryIdList[index]
				this.getGoodsList();
                this.tabIndex = index; //一旦访问data就会出问题
				
			},
			async setScrollLeft(tabIndex){  //设置swiper滑动时顶部tab随着滑动
				let leftWidthSum = 0;
				for(let i = 0;i <= tabIndex;i++){
					let nowElement = await this.getElSize('tabNum'+this.tabBars[i].id);
					leftWidthSum = leftWidthSum + nowElement.width;
					// console.log(nowElement)
				}
				let winWidth = uni.getSystemInfoSync().windowWidth;
				this.scrollLeft = leftWidthSum > winWidth?(leftWidthSum - winWidth) : 0;
			},
			goDetail(item){
				uni.navigateTo({
                    url: '/pages/shops/shop-goods-detail/shop-goods-detail?data-goods-id=' + item.id+'&data-goods-optionId=' + item.optionId
                })
			},
			toShopCartList(){
				uni.navigateTo({
				    url: '/pages/shops/shop-cart-list/shop-cart-list'
				})
			},
		}
	}
